<template>
  <c-scroll>
    <div class="list-wrapper">
      <ul class="list" v-for="(tab, listIndex) in dataList" :key="listIndex">
        <p class="title">
          <span class="division horizontal"></span>
          <span class="name">{{tab.title}}</span>
          <span class="division horizontal"></span>
        </p>
        <div class="banner">
          <c-slide ref="slide" :data="items" @change="changePage">
            <c-slide-item v-for="(item, index) in items" :key="index" @click.native="clickHandler(item, index)">
              <a class="img" :href="item.url" :style="`background:url(${item.imgUrl}) center center/100% auto no-repeat;`"> </a>
            </c-slide-item>
          </c-slide>
        </div>
        <div class="item-wrapper">
          <li class="item" v-for="(item, itemIndex) in tab.list" :key="itemIndex">
            <template v-if="item.type==='banner'">
            </template>
            <template v-else>
              <p class="img" :style="`background:url(${item.imgUrl}) center center/contain no-repeat;`"></p>
              <p class="name">{{item.name}}</p>
              <!-- <a href="javascript:void(0);" class="mask"></a> -->
            </template>
          </li>
        </div>
      </ul>
    </div>

  </c-scroll>
</template>

